import React, { Component } from "react";
import "./storage.scss";
import { Form, Select, Button, Tabs, Collapse } from "antd";
import CheckBox from "@/components/checkBox/checkBox";
import HideSetLayerStorageConfig from "./hideSetLayerStorageConfig/hideSetLayerStorageConfig";

const { Option } = Select;
const { TabPane } = Tabs;
const { Panel } = Collapse;
class Storage extends Component {
  state = {
    selectSet: [
      {
        name: "clean",
        label: "",
        options: [
          { value: "jack", option: "Jack" },
          { value: "lucy", option: "Lucy" },
          { value: "tom", option: "Tom" },
        ],
      },
      {
        name: "time1",
        label: "",
        options: [
          { value: "jack", option: "Jack" },
          { value: "lucy", option: "Lucy" },
          { value: "tom", option: "Tom" },
        ],
      },
      {
        name: "time2",
        label: "",
        options: [
          { value: "jack", option: "Jack" },
          { value: "lucy", option: "Lucy" },
          { value: "tom", option: "Tom" },
        ],
      },
    ],
    GCSConfig: {
      first: {
        title: "1GCS",
      },
      second: {
        title: "2GCS bucket",
        tip: "GCS bucket",
      },
      third: {
        title: "3",
        tip: "",
        list: ["Card content1", "Card content2", "Card content3"],
      },
    },

    S3Config: {
      first: {
        title: "1IAM",
        tip: "IAM",
      },
      second: {
        title: "2S3 bucketS3 bucket",
        tip: "IAMS3",
        list: ["Card content1", "Card content2", "Card content3"],
      },
      third: {
        title: "3",
        tip: "",
        list: ["Card content1", "Card content2", "Card content3"],
      },
    },
  };
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log("Received values of form: ", values);
      }
    });
  };
  onChange(value) {
    console.log(`selected ${value}`);
  }

  callback(key) {
    console.log(key);
  }


  render() {
    const { getFieldDecorator } = this.props.form;
    let { selectSet, GCSConfig, S3Config } = this.state;

    return (
      <div className="storage">
        <div className="agent_storage">
          <div className="header">
            <h3 className="title"></h3>
            <div className="toggle_config">
              <CheckBox label="" onChange={this.onChange} />
            </div>
          </div>
          <Form onSubmit={this.handleSubmit} className="select_set">
            {selectSet.map(item => {
              let { name, label, options } = item;
              return (
                <Form.Item label={label} key={name}>
                  {getFieldDecorator(name)(
                    <Select
                      showSearch
                      style={{ width: 200 }}
                      placeholder=""
                      optionFilterProp="children"
                      onChange={this.onChange}
                      filterOption={(input, option) =>
                        option.props.children
                          .toLowerCase()
                          .indexOf(input.toLowerCase()) >= 0
                      }
                    >
                      {options.map(val => {
                        let { value, option } = val;
                        return (
                          <Option value={value} key={value}>
                            {option}
                          </Option>
                        );
                      })}
                    </Select>
                  )}
                </Form.Item>
              );
            })}
            <Form.Item>
              <Button type="primary" htmlType="submit">
                
              </Button>
            </Form.Item>
          </Form>
        </div>
        <div className="layer_storage">
          <h3 className="title"></h3>
          <p></p>
          <ul>
            <li></li>
            <li></li>
            <li></li>
          </ul>
        </div>
        <div className="hide_set_layer_storage">
          <Collapse defaultActiveKey={["1"]}>
            <Panel header={<h3 className="title"></h3>} key="1">
              <p>GCSS3</p>
              <Tabs onChange={this.callback} type="card">
                <TabPane tab="GCS" key="1">
                  <HideSetLayerStorageConfig data={GCSConfig} />
                </TabPane>
                <TabPane tab="S3" key="2">
                  <HideSetLayerStorageConfig data={S3Config} />
                </TabPane>
              </Tabs>
            </Panel>
          </Collapse>
        </div>
      </div>
    );
  }
}

export default Form.create({ name: "storage" })(Storage);
